// mixin
import saleCommon from '../mixins/saleCommon'
export default {
  name: 'help-Report',
  mixins : [saleCommon],
  data() {
    return {
      areaList:[],
      bankList:[],
      areaValue:[],
      bankValue:[]
    };
  },
  methods: {
    //
    goCount() {
      if (!this.btnStatus) return;
      const ROSE_OPTION = JSON.parse(localStorage.getItem("ROSE_OPTION"));  //
      //
      let governor = this.governor, 
          member = this.member, 
          comments = this.comments;
      // 
      let tips = '';
      let tipArr = [
        { key:'areaValue', msg:'' },
        { key:'bankValue', msg:'' },
        { key:'helpCaptain', msg:'' },
        { key:'governor', msg:'' },
        { key:'member', msg:'' },
        { key:'Aclass', msg:'A' },
        { key:'Bclass', msg:'B' },
        { key:'arr', msg:'' },
      ];
      tipArr.every( item => {
          if(!this[item.key] && this[item.key] !== 0){
            tips = `${item.msg}`
            return;
          }
          else if(item.key === 'areaValue' && !this[item.key].length || this[item.key][0] === '' || item.key === 'bankValue' && !this[item.key].length){
            tips = `${item.msg}`;
            return;
          }
          else if(item.key === 'arr'){
            for(let inx in this[item.key]){
              if(!this[item.key][inx].value.length){
                tips = `${item.msg}`;
              }
              else if(this[item.key][inx].value[0] !== '' && !this[item.key][inx].qty && this[item.key][inx].qty !== 0){
                tips = '';
              }
            }
            return;
          }  
        return true;
      })
      if(tips){
        this.$vux.alert.show({
          content: tips
        })
        return;
      }
      // 
      let jsonData = {
        listId: "4bda3e47-a088-4749-a988-ebb07cfb00e4",
        referenceId: this.guid(),
        baseinfoExt: {
          id: this.guid(),
          varchar1: ROSE_OPTION.dept,         // () ()
          varchar2: this.captain,             // 
          varchar3: this.areaValue[0],        // ()
          varchar4: this.bankValue[0],        // 
          varchar5: ROSE_OPTION.groupName,    // ()
          varchar6: "",                     // 
          varchar7: governor,                 // 
          varchar8: member,                   // 
          varchar9: comments,                 // 
          varchar10: ROSE_OPTION.region,      // ()
          varchar11: ROSE_OPTION.bank,        // ()
          varchar12: ROSE_OPTION.userCode     // 
        },
        transDetailUncalc: [{ 
            id: this.guid(),
            transObjCode: "A", //
            containerCode: "A", //
            qty: "",
            amount: Number(this.Aclass), //
            fgCode: ""
          },
          { id: this.guid(),
            transObjCode: "B", //
            containerCode: "B", //
            qty: "",
            amount: Number(this.Bclass), //
            fgCode: ""
          }
        ],
        transCode: "XHXSDD"
      };
      // 
      for(let i = 0; i < this.arr.length; i++){
        jsonData.transDetailUncalc.push({
          id: this.guid(),
          transObjCode:this.arr[i].value[0] === "" ? "" : this.arr[i].value[0].split("_")[0], //
          containerCode: "", //
          qty: this.arr[i].value[0] === "" ? "" : this.arr[i].qty,
          taxAmount: this.arr[i].value[0] === "" ? "" : Number(this.arr[i].value[0].split("_")[2]),
          amount: this.arr[i].qty * this.arr[i].value[0].split('_')[3], //
          fgCode: ""
        });
      }
      let totalInfo = {
        isMobile: true,
        conn: 20000,
        list: "trans_form_data",
        transCode: "XHXSDD",
        jsonData: JSON.stringify(jsonData)
      };
      this.totalInfo = totalInfo;
      //  
      localStorage.setItem(
        "saleReportInfo",
        JSON.stringify({
          saleReportRemark: totalInfo,
          time: new Date().getTime()
        })
      );
      //   
      localStorage.setItem(
        "HELP_FORM_INFO",
        JSON.stringify({
          Aclass: this.Aclass,
          Bclass: this.Bclass,
          saleReportArr: this.arr,
          comments : this.comments,
          time: new Date().getTime()
        })
      );
      //  
      localStorage.setItem(
        "HELP_BASIC_INFO",
        JSON.stringify({
          member: this.member,
          governor: this.governor,
          captain: this.helpCaptain,
          bank: this.bankValue[0],
          areaValue: this.areaValue[0],
        })
      );          

      this.$router.push({ path: "/count" });
    }
  },
  created(){
    // 
    const HELP_BASIC_INFO = JSON.parse(localStorage.getItem("HELP_BASIC_INFO")) || '';
    const HELP_FORM_INFO = JSON.parse(localStorage.getItem("HELP_FORM_INFO")) || '';
    // 
    this.echoStorage(HELP_BASIC_INFO, HELP_FORM_INFO);
    // 
    this.getArea();
    // 
    this.getBank();
  },
  beforeRouteLeave(to, from, next) {
    if (!this.arr[0].value.length || to.name == "Count") next();
    else {
      this.$vux.confirm.show({
        title: "",
        content: "",
        confirmText: "",
        cancelText: "",
        onCancel:() => {
          localStorage.removeItem("HELP_FORM_INFO");
          next();
        },
        onConfirm:() => {
          localStorage.setItem(
            "HELP_FORM_INFO",
            JSON.stringify({
              Aclass: this.Aclass,
              Bclass: this.Bclass,
              saleReportArr: this.arr,
              comments : this.comments,
              time: new Date().getTime()
            })
          );
          //
          localStorage.setItem(
            "HELP_BASIC_INFO",
            JSON.stringify({
              member: this.member,
              governor: this.governor,
              captain: this.helpCaptain,
              bank: this.bankValue[0],
              areaValue: this.areaValue[0],
            })
          );          
          next();
        }
      });
    }
  }
};



